/* heebo-100normal - latin */
@font-face {
  font-family: 'Heebo';
  font-style: normal;
  font-display: swap;
  font-weight: 100;
  src:
    local('Heebo Thin '),
    local('Heebo-Thin'),
    url('./files/heebo-latin-100.woff2') format('woff2'), /* Super Modern Browsers */
    url('./files/heebo-latin-100.woff') format('woff'); /* Modern Browsers */
}

